export default {
	data() {
		return {
			current: 0,
			tabs: [{
				name: '置换'
			}, {
				name: '置换中'
			}, {
				name: '已完成'
			}, {
				name: '往日完成'
			}],
			page: 0,
			pages: 1,
			type: 0,
			loadshow: true,
			scrlist: [],
			usermid: uni.getStorageSync('member').mid
		}
	},
	methods: {
		cancall(e) {
			var self = this;
			uni.showModal({
				title: "提示",
				content: "确定取消吗?",
				success:(res)=>{
					if (res.confirm) {
						self.$service.auth(self, self.$service.api.center.cancelPurchasingOrder, 'GET', {
							id: e
						}, function(self, res) {
							console.log(res);
							uni.showToast({
								title:res.info,
								icon:'none'
							});
							self.page = 0;
							self.pages=1;
							self.loadshow=true;
							self.scrlist = [];
							self.Loadthepage();
						});

						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})



		},
		jump(e) {
			uni.switchTab({
				url: e
			})
		},
		deail(e) {
			uni.navigateTo({
				url: '/pages/record/order?id=' + e
			})
		},
		Loadthepage() {
			var self = this;
			if (!self.loadshow) {
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				self.$service.auth(self, self.$service.api.center.getTransactionInfo, "GET", {
					page: page,
					limit: 15,
					type: self.type,

				}, function(self, res) {
					console.log(res, "123213")
					self.pages = res.data.last_page;
					self.page = page;
					// self.activity = res.data.activity
					var list = res.data.data;
					list.forEach((v, i) => {
						self.scrlist.push(v);
					});
					if (self.pages == self.page || self.scrlist.length == 0) {
						console.log("false");
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						console.log("2" , self.page , self.pages );
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},
		
		tabChange(index) {
			this.current = index;
			this.type = index;
			this.scrlist = [];
			this.page = 0,
				this.pages = 1,
				this.loadshow = true,
				this.Loadthepage();
		}
		/* showModel() {
			uni.showModal({
				title: '取消置换',
				content: '是否确认取消该置换',
				cancelColor: '#999',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					}
				}
			});
		} */
	},
	onReachBottom() {
		console.log('121')
		this.Loadthepage();
	},
	onLoad(op) {
		if(op.type == undefined){
			this.type = 0;
			this.current = 0
		}else{
			this.type = op.type;
		this.current = op.type;
		}
		
		/* this.page = 0;
		this.pages = 1;
		this.scrlist = [] */
		this.Loadthepage();
	},
	/* onShow() {
		// this.loadshow = true
		this.Loadthepage()
		this.$service.auth(this, this.$service.api.center.getTransactionInfo, "GET", {
			page: 1,
			limit: 15,
			type: 1
		}, function(self, res) {
			console.log(res)
		})
	} */
}
